import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroupDirective, NgForm, Validators} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';
import { Title } from '@angular/platform-browser';
import { MatSnackBar } from '@angular/material';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  user:string;
  password:string;
  col:number = 2;
  userFormControl = new FormControl('', [
    Validators.required
  ]);

  passwordFormControl = new FormControl('', [
    Validators.required
    ]);

  matcher = new MyErrorStateMatcher();

  constructor(
    private _title:Title,
    private _snackBar:MatSnackBar,
    private _auth:AuthService,
    private _router:Router
  ) { 
    window.addEventListener('resize', ()=>{
      if (window.innerWidth < 779) {
        this.col = 1;

      }else{
        this.col = 2
      }

    })
    if (window.innerWidth < 778) {
      this.col = 1;
    }else{
      this.col = 2
    }

  }
  ngOnInit() {
    window.scrollTo(0,0)

    this._title.setTitle('YODA | Login')
  }

  login(){
    if(this.userFormControl.hasError('required') || this.passwordFormControl.hasError('required')){
      this._snackBar.open('plz check username and password', 'Undo', {duration: 3000});
      return false;
    }
    let user = {
      user:this.user,
      password:this.password
    }
    this._auth.loginUser(user).subscribe((res:any)=>{
      if (res.success){
        this._auth.storeUserData(res.token, res.user);
        this._router.navigate(['/user'])
        this._snackBar.open('welcome', 'Undo', {duration: 3000});
      }else{
        this._snackBar.open(res.errMSG, 'Undo', {duration: 10000});
      }
    },
    (error)=>{
      this._snackBar.open(error.message, 'Undo', {duration: 10000});
    })
  }

}
