import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { ErrorComponent } from './shared/sharedComponents/error/error.component';
import { RegisterComponent } from './components/register/register.component';
import { LoginGuardGuard } from './guards/login-guard.guard';

const routes: Routes = [
  {path:'login',component:LoginComponent,  data:{depth:'login'} },
  {path:'', redirectTo:'login', pathMatch:'full', data:{depth:'login'}},
  {path:'register', component:RegisterComponent, data:{depth:'register'}},
  {path:'user', loadChildren:'./user/user.module#UserModule', data:{depth:'home'}, 
  canActivate:[LoginGuardGuard]
},
  
  {path:'**', component:ErrorComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routecomponents = [
  LoginComponent,
  RegisterComponent,
]
