import { Injectable } from '@angular/core';
import { HttpErrorResponse, HttpClient } from '@angular/common/http';
import { throwError, BehaviorSubject } from 'rxjs';
import { Router } from '@angular/router';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ServicesService {
  // url='';
  url:string = 'http://localhost:3000';
  private friendsReq = new BehaviorSubject<number>(0);
  friendsReqLength = this.friendsReq.asObservable();
  constructor(
    private _http:HttpClient,
    private _router:Router
  ) { }

  changefriendsReq(num){
    this.friendsReq.next(num);
  }

  getPosts(userId){
    return this._http.get(`${this.url}/post/${userId}/user`).pipe( catchError(this.handleError));
  } 

  editPost(id, body){
    return this._http.post(`${this.url}/post/addText/${id}/edit`, body).pipe( catchError(this.handleError));
  }
  removePost(id){
    return this._http.get(`${this.url}/post/addText/${id}/remove`).pipe( catchError(this.handleError));
  }
  addPost(po){
    return this._http.post(`${this.url}/post/addText`, po).pipe( catchError(this.handleError));
  }
  postComment(comment, id){
    return this._http.post(`${this.url}/post/${id}/comment`, comment).pipe( catchError(this.handleError));
  }
  editComment(postId, commentId, data){
    return this._http.post(`${this.url}/post/${postId}/${commentId}/comment/edit`, data).pipe( catchError(this.handleError));
  }
  removeComment(postId, commentId){
    return this._http.get(`${this.url}/post/${postId}/${commentId}/comment/remove`).pipe( catchError(this.handleError));
  }
  postReplay(id, commentId, replay){
    return this._http.post(`${this.url}/post/${id}/${commentId}/replay`, replay).pipe( catchError(this.handleError));
  }
  editReplay(postId, commentId, replayId, data){
    return this._http.post(`${this.url}/post/${postId}/${commentId}/${replayId}/replay/edit`, data).pipe( catchError(this.handleError));
  }
  removeReplay(postId, commentId, replayId){
    return this._http.get(`${this.url}/post/${postId}/${commentId}/${replayId}/replay/remove`).pipe( catchError(this.handleError));
  }
  getUserFriends(id){
    return this._http.get(`${this.url}/users/${id}/user/friends`).pipe( catchError(this.handleError));
  }
  findFriend(username, id){
    return this._http.get(`${this.url}/users/find/${username}/${id}`).pipe( catchError(this.handleError));
  }

  findAllFriend(username, id){
    return this._http.get(`${this.url}/users/find/all/${username}/${id}`).pipe( catchError(this.handleError));
  }

  getUserById(id){
    return this._http.get(`${this.url}/users/${id}`).pipe( catchError(this.handleError));
  }
  find(){
    return this._http.get(`${this.url}/users/find`).pipe( catchError(this.handleError));
  }
  sendfriendRequest(id, friendId){
    return this._http.post(`${this.url}/users/${id}/addToFriends`, friendId).pipe( catchError(this.handleError));
  }
  getfriendRequest(id){
    return this._http.get(`${this.url}/users/${id}/friendsRequst`).pipe( catchError(this.handleError));
  }

  ignnorFriendReq(id, reqId){
    return this._http.get(`${this.url}/users/${id}/${reqId}/ignnorFriendRequst`).pipe( catchError(this.handleError));
  }
  acceptFriendReq(id, reqId){
    return this._http.get(`${this.url}/users/${id}/${reqId}/acceptFriendRequst`).pipe( catchError(this.handleError));
  }
  getUserPosts(id){
    return this._http.get(`${this.url}/post/${id}`).pipe( catchError(this.handleError));
  }
  getPostComment(id){
    return this._http.get(`${this.url}/post/${id}/all/comment`).pipe( catchError(this.handleError));
  }
  unfriend(userId, friendId){
    return this._http.get(`${this.url}/users/find/${userId}/${friendId}/unfriend`).pipe( catchError(this.handleError));
  }
  cancelFriendReqFromSender(userId, friendId){
    return this._http.get(`${this.url}/users/${userId}/${friendId}/canselRequstFromSender`).pipe( catchError(this.handleError));
  }
  // getMessages(){
  //   return this._http.get(`${this.url}/message`).pipe( catchError(this.handleError))
  // }
  sendMessage(id, message){
    return this._http.post(`${this.url}/message/${id}`, message).pipe( catchError(this.handleError))
  }

  // removeMessages(id){
  //   return this._http.get(`${this.url}/message/${id}/remove`).pipe( catchError(this.handleError))
  // }

  handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      return throwError(
        'Something bad happened; please try again later.' + error.message);
    } else {
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(error);
  };

  getProfile(id){
    localStorage.setItem('profileId', id);
    this._router.navigate(['/profile']);
    // this.changeProfileId(id)

  }
  

  // password valdation
  passwordValdation(password){
    var t = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[A-Za-z\d$@$!%*?&]{8,}/
        if(!t.test(password)){
          return false;
        }
        else{
          return true
        }
    }  // password valdation


}
